    .profileDescription{
        width: 96%;
        align-self: center;
        height: 33%;
        background: rgba(121, 121, 121, 0.445);
        opacity: 0.7;
        text-align: center;
        font-family: "Press Start 2P", system-ui;
        font-weight: 300;
        font-style: normal;
        padding-top: 1%;
        padding-bottom: 1%;
        font-size: 11px;
        color: white;
        border: 1px solid rgb(15, 184, 15);
        border-right: 10px solid green;
        border-left: 10px solid green;
        line-height: 35px;
       
    
        }

        .pestanas{
    
           justify-content: space-between;
           width: 90%;
           height: 40%;

        }

        .botonesHeader{

            text-decoration: none;
            display: flex;
            flex-direction: row;
            list-style: none;
            font-family: "Press Start 2P", system-ui;
            justify-content: space-around;
        }

        .botonesHeader> li{
         
            width: 200px;
            height: 120px;
            margin-left: 10px;
            background-color: transparent;
            color: white;
            border-color: solid 10px black;
           

        }

         .botonesHeader> li > button{
         
            width: 220px;
            height: 80px;
            margin-left: 5px;
            background-color: transparent;
            color: rgb(46, 23, 23);
            opacity: 0.9;
            border: 1px solid green;
            border-right: 5px solid green;
             font-family: "Press Start 2P", system-ui;
            font-style: normal;
            border-radius: 5%;
            color: white;
            
        
        }
       