@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

.ContainP{

width: 100%;
height: 100%;
background-color: transparent;
display:grid;
grid-template-columns: repeat(3,1fr);
column-gap: 30px;
row-gap: 50px;

}
.tarjetaP{
transform: translate(10px, 20px);
width: 340px;
height: 250px;
border: 5px solid white;
border-radius: 30px;
background-color: white;


}

.tarjetaP >img {
    width: 300px;
    height: 140px;
    margin-left: 10px;
    margin-top: 10px;

}

 .tarjetaP:hover{

    transform:scaleZ(1.1);

} 

.tarjetaP >p{
    font-family: "Press Start 2P", system-ui;
    font-weight: 30;
    font-style: normal;

}

.ver {

width: 280px;
height: 40px;
background-color: black;
color: green;
transform: translate(20px, -15px);
}
.ver:hover {


background-color: white;
color: black;

}
.background{

z-index: -10;
position:absolute;
padding-top: 50px;

height: 1000px;
}
